.habilidades-container {
    height: 100vh;
    flex-direction: column;
    justify-content: space-around;
    display: flex;
    align-content: center;
    align-items: center;
    background-color: #252131cc;
}

.barrinha-container {
    border: 2px solid white;
    flex: 1;
    display: flex;
    border-radius: 30px;
    flex-direction: row;
    margin-left: 20px;
    margin-right: 20px;

}

.habilidade-container {
    display: flex;
    flex-direction: row;
    width: 100%;    
}

.preenchimento-barrinha {
    background-color: #888888;
    border-radius: 30px;
}

.nome-habilidade {
    margin-left: 20px;
}